<template>
  <div class="table">
    <el-table
      ref="singleTable"
      :border="isBorder"
      :data="tableContent"
      align="center"
      v-loading="loading"
      header-align="center"
      highlight-current-row
      @current-change="handleCurrentChange"
      @selection-change="handleSelectionChange"
      style="width: 100%"
    >
      <template v-if="isSelection">
        <el-table-column type="selection" width="55"></el-table-column>
      </template>
      <template v-for="v in tableHeader">
        <slot v-if="v.columnInfoShow" :name="v.columnInfoShow"></slot>
        <el-table-column
          :type="v.type"
          :property="v.type"
          :label="v.label"
          :key="v.index"
          v-if="v.type"
          :width="v.width"
          :formatter="v.formatter"
          show-overflow-tooltip
          align="center"
        ></el-table-column>
        <el-table-column
          fixed="right"
          :label="v.label"
          :width="v.width || 120"
          align="center"
          v-else
        >
          <template slot-scope="scope">
            <el-button
              v-for="e in v.detail"
              v-if="e.value && scope.row.value == '' "
              type="text"
              :key="e.index"
              @click="$emit(e.event,scope.row)"
              disabled
            >{{e.operation}}</el-button>
            <el-button
              @click="$emit(e.event,scope.row)"
              type="text"
              :key="e.index"
              v-else
            >{{e.operation}}</el-button>
          </template>
        </el-table-column>
      </template>
    </el-table>
  </div>
</template>
<style lang="scss">
.table {
  margin-top: 20px;
  /*.el-table {*/
  /*border: 1px solid #ebeef5;*/
  /*}*/
}
</style>
<script>

export default {
  data () {
    return {
      loading: true
    }
  },
  created () {

  },
  mounted () {
    this.loading = this.loadingFlag === false ? false : true;
    // this.loading = true
  },
  props: [
    "tableHeader",
    "tableContent",
    'isSelection',
    'loadingFlag',
    'isBorder'
  ],
  methods: {
    handleCurrentChange (val) {
      this.currentRow = val;
    },
    handleSelectionChange (val) {
      this.$emit('selectionChange', val)
    },
    handleClick (row) {
      //        console.log(row);
    }
  },
  watch: {
    tableContent (val, oldval) {
      this.loading = false
    },
  },
}
</script>



