<template>
  <div class="wrapper">
    <header>任务列表</header>

    <div class="search-box">
      <div class="search-boxItem">
        <el-input placeholder="请输入任务名称" prefix-icon="el-icon-search" v-model="taskName"></el-input>
      </div>
      <div class="search-boxItem">
        <el-button type="primary" plain @click="addNewTask">新增任务</el-button>
      </div>
    </div>

    <!-- <div class="table-box">
      <div class="table-title">
        <span class="leftline"></span>
        数据查询时间{{time}}
        <span>&nbsp|&nbsp</span>
        共查询{{dataNum}}条
      </div>
    </div>-->

    <div class="table">
      <table-list
        :tableHeader="TableHeader"
        :tableContent="TableData"
        @lookDetailFn="lookDetailFn($event)"
        :loadingFlag="loading"
        :isBorder="border"
      ></table-list>
    </div>
  </div>
</template>
<script>
import paging from "@/components/Paging/Paging";
import TableList from "@/components/Table/Table";
export default {
  data() {
    return {
      border: true,
      loading: false,
      taskName: "",
      timer: null,
      TableHeader: [
        {
          label: "序号",
          type: "index"
        },
        {
          label: "任务名称",
          type: "name"
        },
        {
          label: "状态",
          type: "state"
        },
        {
          label: "开始时间",
          type: "type"
        },
        {
          label: "结束时间",
          type: "jsstandard"
        },
        {
          label: "任务列表",
          type: "klstandard"
        },
        {
          label: "总预算",
          type: "klstandard"
        },
        {
          label: "人群及预算占比",
          type: "klstandard"
        },
        {
          label: "操作",
          detail: [
            {
              operation: "详情",
              event: "lookDetailFn"
            }
          ]
        }
      ],
      TableData: [
        {
          name: "发多少",
          state: "正常",
          type: "CPS",
          jsstandard: "12",
          klstandard: "1"
        }
      ]
    };
  },
  components: {
    TableList
  },
  watch: {
    taskName(val) {
      let _this = this;
      clearTimeout(_this.timer);
      _this.timer = null;
      _this.timer = setTimeout(function() {
        console.log(val);
      }, 500);
    }
  },
  methods: {
    addNewTask(){
      this.$router.push('/NewTask')
    },
    lookDetailFn() {}
  }
};
</script>
<style scoped lang="scss">
.wrapper {
  header {
    color: #000;
    box-sizing: border-box;
    border-bottom: 1px solid #008cff;
    padding: 0 0 10px 10px;
    font-weight: 600;
    font-size: 18px;
    margin-bottom: 20px;
  }
  .search-box {
    display: flex;
    align-items: center;
    box-sizing: border-box;
    // border: 1px solid #a5a5a5;
    border-radius: 3px;
    padding: 0 15px;
    justify-content: space-between;
    .search-boxItem {
      margin: 10px 0;
    }
    .el-input {
      min-width: 200px;
      width: 15%;
      margin-right: 15px;
    }
    .el-select {
      margin-right: 15px;
    }
  }
  .table-box {
    .table-title {
      font-size: 16px;
      color: #333;
      font-weight: 600;
      height: 20px;
      line-height: 20px;
      display: flex;
      align-items: center;
      box-sizing: initial;
      padding: 10px;
      .leftline {
        display: block;
        height: 100%;
        width: 3px;
        background: #333;
        margin-right: 5px;
      }
    }
    .count-box {
      margin-top: 15px;
      display: flex;
      align-items: center;
      font-size: 14px;
      .box {
        padding: 0 30px;
        .title {
          font-weight: 600;
          color: #333;
          margin-bottom: 10px;
        }
        .result {
          color: #a5a5a5;
          text-align: center;
        }
      }
    }
    .table {
      margin-top: 15px;
    }
  }
}
</style>



